import { NotificationService } from '../notificationService';
import { MatchingService } from '../matchingService';
import { Pet, IPet } from '../../models/Pet';
import { User } from '../../models/User';
import { NotificationType, NotificationPriority } from '../../models/Notification';
import { logger } from '../../utils/logger';

export interface GeofenceArea {
  center: [number, number]; // [longitude, latitude]
  radius: number; // in kilometers
  userId: string;
  petId: string;
  name?: string;
  isActive: boolean;
  createdAt: Date;
}

export interface GeofenceConfig {
  enabled: boolean;
  defaultRadius: number; // in kilometers
  checkInterval: number; // in minutes
}

export class GeofenceService {
  private static config: GeofenceConfig = {
    enabled: true,
    defaultRadius: 10, // 10km
    checkInterval: 15 // 15 minutes
  };
  
  private static geofenceAreas: Map<string, GeofenceArea> = new Map();
  private static interval: NodeJS.Timeout | null = null;

  /**
   * ÂàùÂßãÂåñÂú∞ÁêÜÂúçÊ¨ÑÊúçÂãô
   */
  static async initialize(config?: Partial<GeofenceConfig>): Promise<void> {
    if (config) {
      this.config = { ...this.config, ...config };
    }

    // ËºâÂÖ•ÁèæÊúâÁöÑÂú∞ÁêÜÂúçÊ¨Ñ
    await this.loadGeofenceAreas();

    this.start();
    logger.info('Âú∞ÁêÜÂúçÊ¨ÑÊúçÂãôÂ∑≤ÂàùÂßãÂåñ', { config: this.config });
  }

  /**
   * ÂïüÂãïÂú∞ÁêÜÂúçÊ¨Ñ‰ªªÂãô
   */
  static start(): void {
    if (!this.config.enabled || this.interval) return;

    const intervalMs = this.config.checkInterval * 60 * 1000;
    
    this.interval = setInterval(async () => {
      try {
        await this.processGeofenceNotifications();
      } catch (error) {
        logger.error('Âú∞ÁêÜÂúçÊ¨ÑÈÄöÁü•‰ªªÂãôÂ§±Êïó', { error });
      }
    }, intervalMs);

    logger.info('Âú∞ÁêÜÂúçÊ¨ÑÈÄöÁü•‰ªªÂãôÂ∑≤ÂïüÂãï', { 
      interval: this.config.checkInterval 
    });
  }

  /**
   * ÂÅúÊ≠¢Âú∞ÁêÜÂúçÊ¨Ñ‰ªªÂãô
   */
  static stop(): void {
    if (this.interval) {
      clearInterval(this.interval);
      this.interval = null;
      logger.info('Âú∞ÁêÜÂúçÊ¨ÑÈÄöÁü•‰ªªÂãôÂ∑≤ÂÅúÊ≠¢');
    }
  }

  /**
   * ËôïÁêÜÂú∞ÁêÜÂúçÊ¨ÑÈÄöÁü•
   */
  private static async processGeofenceNotifications(): Promise<void> {
    try {
      // Ê™¢Êü•ÊØèÂÄãÊ¥ªË∫çÁöÑÂú∞ÁêÜÂúçÊ¨Ñ
      for (const [areaId, area] of this.geofenceAreas) {
        if (!area.isActive) continue;

        try {
          await this.checkGeofenceArea(area);
        } catch (error) {
          logger.error('Ê™¢Êü•Âú∞ÁêÜÂúçÊ¨ÑÂ§±Êïó', { 
            areaId, 
            error 
          });
        }
      }
    } catch (error) {
      logger.error('Âú∞ÁêÜÂúçÊ¨ÑËôïÁêÜÂ§±Êïó', { error });
    }
  }

  /**
   * Ê™¢Êü•Âú∞ÁêÜÂúçÊ¨ÑÂçÄÂüü
   */
  private static async checkGeofenceArea(area: GeofenceArea): Promise<void> {
    const targetPet = await Pet.findById(area.petId);
    if (!targetPet) {
      // ÂØµÁâ©‰∏çÂ≠òÂú®ÔºåÁßªÈô§Âú∞ÁêÜÂúçÊ¨Ñ
      this.geofenceAreas.delete(area.petId);
      return;
    }

    // Â¶ÇÊûúÂØµÁâ©Â∑≤ÊâæÂà∞ÔºåÂÅúÁî®Âú∞ÁêÜÂúçÊ¨Ñ
    if (targetPet.status === 'reunited') {
      area.isActive = false;
      return;
    }

    // Êü•ÊâæÂúçÊ¨ÑÂÖßÁöÑÁõ∏ÈóúÂØµÁâ©
    const targetStatus = targetPet.status === 'lost' ? 'found' : 'lost';
    const recentThreshold = new Date(Date.now() - 24 * 60 * 60 * 1000); // 24Â∞èÊôÇÂÖß

    const nearbyPets = await Pet.find({
      _id: { $ne: area.petId },
      status: targetStatus,
      createdAt: { $gte: recentThreshold },
      location: {
        $geoWithin: {
          $centerSphere: [
            area.center,
            area.radius / 6371 // ËΩâÊèõÁÇ∫ÂºßÂ∫¶
          ]
        }
      }
    }).populate('owner');

    if (nearbyPets.length > 0) {
      await this.sendGeofenceNotification(area, targetPet, nearbyPets);
    }
  }

  /**
   * ÁôºÈÄÅÂú∞ÁêÜÂúçÊ¨ÑÈÄöÁü•
   */
  private static async sendGeofenceNotification(
    area: GeofenceArea,
    targetPet: IPet,
    nearbyPets: IPet[]
  ): Promise<void> {
    const distances = nearbyPets.map(pet => {
      const distance = MatchingService.calculateDistance(
        area.center,
        // Êö´ÊôÇË∑≥ÈÅéÂú∞ÁêÜÂùêÊ®ôÊ™¢Êü•ÔºåÂõ†ÁÇ∫ Pet Ê®°Âûã‰∏≠Ê≤íÊúâ location.coordinates Â≠óÊÆµ
        // TODO: Ê∑ªÂä†Âú∞ÁêÜÂùêÊ®ôÂ≠óÊÆµÂà∞ Pet Ê®°Âûã
        [0, 0] // Êö´ÊôÇ‰ΩøÁî®ÈªòË™çÂùêÊ®ô
      );
      return { pet, distance };
    }).sort((a, b) => a.distance - b.distance);

    const closestPet = distances[0];

    if (closestPet) {
      await NotificationService.sendNotification({
        userId: area.userId,
        type: 'GEOFENCE_ALERT' as NotificationType,
        title: 'üìç Âú∞ÁêÜÂúçÊ¨ÑË≠¶Â†±',
        message: `Âú®ÊÇ®Ë®≠ÂÆöÁöÑ ${area.radius}km ÁØÑÂúçÂÖßÁôºÁèæ‰∫Ü ${nearbyPets.length} Èöª${targetPet.status === 'lost' ? 'ÊãæÁç≤' : 'Â§±Ëπ§'}ÁöÑÂØµÁâ©ÔºåÊúÄËøëË∑ùÈõ¢ ${Math.round(closestPet.distance * 100) / 100}km`,
        data: {
          petId: targetPet._id.toString(),
          geofenceId: area.petId,
          nearbyCount: nearbyPets.length,
          closestDistance: Math.round(closestPet.distance * 100) / 100,
          nearbyPets: distances.slice(0, 3).map(d => ({
            petId: d.pet._id.toString(),
            name: d.pet.name,
            distance: Math.round(d.distance * 100) / 100
          }))
        },
        priority: NotificationPriority.HIGH
      });
    }
  }

  /**
   * ÂâµÂª∫Âú∞ÁêÜÂúçÊ¨Ñ
   */
  static async createGeofence(data: {
    userId: string;
    petId: string;
    latitude: number;
    longitude: number;
    radius?: number;
    name?: string;
  }): Promise<GeofenceArea> {
    const { userId, petId, latitude, longitude, radius, name } = data;
    const center: [number, number] = [longitude, latitude];
    
    const geofenceArea: GeofenceArea = {
      center,
      radius: radius || this.config.defaultRadius,
      userId,
      petId,
      name: name || `${petId} ÁöÑÂú∞ÁêÜÂúçÊ¨Ñ`,
      isActive: true,
      createdAt: new Date()
    };

    this.geofenceAreas.set(petId, geofenceArea);
    
    // ‰øùÂ≠òÂà∞Ë≥áÊñôÂ∫´ÔºàÂèØÈÅ∏Ôºâ
    await this.saveGeofenceArea(geofenceArea);

    logger.info('Âú∞ÁêÜÂúçÊ¨ÑÂ∑≤ÂâµÂª∫', { 
      userId, 
      petId, 
      center, 
      radius: geofenceArea.radius 
    });

    return geofenceArea;
  }

  /**
   * ÁßªÈô§Âú∞ÁêÜÂúçÊ¨Ñ
   */
  static async removeGeofence(userId: string, geofenceId: string): Promise<boolean> {
    const geofence = this.geofenceAreas.get(geofenceId);
    
    if (!geofence || geofence.userId !== userId) {
      throw new Error('Âú∞ÁêÜÂúçÊ¨Ñ‰∏çÂ≠òÂú®ÊàñÁÑ°Ê¨äÈôê');
    }
    
    const removed = this.geofenceAreas.delete(geofenceId);
    
    if (removed) {
      // ÂæûË≥áÊñôÂ∫´ÁßªÈô§ÔºàÂèØÈÅ∏Ôºâ
      await this.deleteGeofenceArea(geofenceId);
      logger.info('Âú∞ÁêÜÂúçÊ¨ÑÂ∑≤ÁßªÈô§', { userId, geofenceId });
    }

    return removed;
  }

  /**
   * Áç≤ÂèñÁî®Êà∂ÁöÑÂú∞ÁêÜÂúçÊ¨ÑÂàóË°®
   */
  static async getUserGeofences(userId: string): Promise<GeofenceArea[]> {
    const userGeofences: GeofenceArea[] = [];
    
    for (const [petId, geofence] of this.geofenceAreas.entries()) {
      if (geofence.userId === userId && geofence.isActive) {
        userGeofences.push(geofence);
      }
    }
    
    return userGeofences;
  }

  /**
   * ËºâÂÖ•Âú∞ÁêÜÂúçÊ¨ÑÂçÄÂüü
   */
  private static async loadGeofenceAreas(): Promise<void> {
    // ÈÄôË£°ÂèØ‰ª•ÂæûË≥áÊñôÂ∫´ËºâÂÖ•Â∑≤‰øùÂ≠òÁöÑÂú∞ÁêÜÂúçÊ¨Ñ
    // Êö´ÊôÇ‰ΩøÁî®Ë®òÊÜ∂È´îÂ≠òÂÑ≤
    logger.info('Âú∞ÁêÜÂúçÊ¨ÑÂçÄÂüüÂ∑≤ËºâÂÖ•');
  }

  /**
   * ‰øùÂ≠òÂú∞ÁêÜÂúçÊ¨ÑÂçÄÂüü
   */
  private static async saveGeofenceArea(area: GeofenceArea): Promise<void> {
    // ÈÄôË£°ÂèØ‰ª•‰øùÂ≠òÂà∞Ë≥áÊñôÂ∫´
    // Êö´ÊôÇË∑≥ÈÅé
  }

  /**
   * Âà™Èô§Âú∞ÁêÜÂúçÊ¨ÑÂçÄÂüü
   */
  private static async deleteGeofenceArea(petId: string): Promise<void> {
    // ÈÄôË£°ÂèØ‰ª•ÂæûË≥áÊñôÂ∫´Âà™Èô§
    // Êö´ÊôÇË∑≥ÈÅé
  }

  /**
   * Áç≤ÂèñÈÖçÁΩÆ
   */
  static getConfig(): GeofenceConfig {
    return { ...this.config };
  }

  /**
   * Êõ¥Êñ∞ÈÖçÁΩÆ
   */
  static updateConfig(newConfig: Partial<GeofenceConfig>): GeofenceConfig {
    this.config = { ...this.config, ...newConfig };
    
    // Â¶ÇÊûúÂïüÁî®ÁãÄÊÖãÊîπËÆäÔºåÈáçÊñ∞ÂïüÂãïÊàñÂÅúÊ≠¢ÊúçÂãô
    if (newConfig.enabled !== undefined) {
      this.stop();
      if (newConfig.enabled) {
        this.start();
      }
    }
    
    logger.info('Âú∞ÁêÜÂúçÊ¨ÑÈÖçÁΩÆÂ∑≤Êõ¥Êñ∞', { config: this.config });
    return { ...this.config };
  }

  /**
   * Áç≤ÂèñÁµ±Ë®à‰ø°ÊÅØ
   */
  static getStats(): {
    enabled: boolean;
    running: boolean;
    totalAreas: number;
    activeAreas: number;
    config: GeofenceConfig;
  } {
    const activeAreas = Array.from(this.geofenceAreas.values())
      .filter(area => area.isActive).length;

    return {
      enabled: this.config.enabled,
      running: this.interval !== null,
      totalAreas: this.geofenceAreas.size,
      activeAreas,
      config: { ...this.config }
    };
  }
}